import React from "react";
import "../css/Tema.css";
export default (props) => {
	return (
		<div className="tema">
			<div className="bgtema"></div>
			{/* <img className="image" src="https://i.pinimg.com/originals/a1/d5/5b/a1d55b0a8d39afe884df8c0905ae7483.png"></img> */}
			<h1 className="texto">
				<h2>A Raposa</h2>
				<p />
				&emsp; O totem da raposa é universal, é um animal que sobrevive no
				deserto, no ártico e em qualquer habitat da terra, o que o torna famoso
				pela sua adaptabilidade ao meio ambiente.
				<p />
				&emsp; A raposa vive em qualquer lugar do mundo desde tempos imemoriais
				e faz parte do folclore e da filosofia de todos os continentes.
				<p />
				&emsp; A sabedoria e astúcia da raposa incluem: Mudança de forma,
				Destreza, Habilidades de observação, Disfarce, Camuflagem, Valor
				feminino, Invisibilidade, Capacidade de observar o que aos outros passa
				despercebido, Persistência, Gentileza e Velocidade.
				<p />
				&emsp; A raposa simboliza não apenas a astúcia dada pela inteligência,
				mas também a capacidade de encontrar soluções para novos problemas que
				surgem.
				<p />
				&emsp; Se nenhum animal encontrar uma maneira de cruzar o rio, a raposa
				encontrará um tronco e o empurrará, ou encontrará um atalho.
				<p />
				&emsp; Além disso, a raposa ensina-nos a capacidade de nos tornarmos um
				com o meio ambiente. Finalmente, deve-se notar que a raposa é
				considerada a protetora da unidade familiar, principalmente na
				adversidade ou nos maus momentos, já que a raposa domina o caminho para
				sobreviver mesmo nos invernos mais rigorosos.
				<p />
				&emsp; Na China, o simbolismo da raposa gira em torno da vida após a
				morte. A crença popular diz que o avistamento de uma raposa é um sinal
				do espírito dos falecidos.
				<p />
				&emsp; No simbolismo celta, a raposa era um guia e era reconhecida pela
				sua sabedoria. Os celtas dizem que a raposa conhece as florestas
				intimamente, e é por isso que seria um grande guia no mundo espiritual.
				<p />
				&emsp; No Japão, a raposa é considerada um dos espíritos da chuva, e um
				mensageiro do Inari, o deus do arroz. No Japão a raposa também simboliza
				a longevidade e a proteção contra o mal. No entanto, na China, diz-se
				que os espíritos das raposas mantêm os homens longe de suas esposas, e é
				por isso que chamam as amantes dos seus maridos de suas "raposas" .
				<p />
				&emsp; Na tradição nativa americana, o simbolismo da raposa tem duas
				interpretações. Nas tribos do norte a raposa era vista como um
				mensageiro inteligente e nobre. Em contraste, nas tribos das pradarias,
				a raposa é considerada uma trapaceira, que gasta piadas pesadas ou, pior
				ainda, atrai a morte. Mas, em geral, todas as culturas concordam em
				relacionar a raposa com astúcia, inteligência, pensamento ágil,
				sagacidade e sabedoria.
				<p />
				&emsp; A raposa totémica encoraja-nos a pensar fora da caixa, a
				estimular a inteligência e a usar a nossa inteligência de uma maneira
				diferente e criativa. A raposa também nos traz a mensagem de tentar ver
				as nossas circunstâncias de um modo diferente do habitual. A raposa diz
				para estarmos cientes de alguns dos nossos hábitos e tentar outro ponto
				de vista da ação.
				<p />
				&emsp; A raposa também é um lembrete de que devemos usar todos os nossos
				recursos (visíveis e invisíveis) para alcançar os nossos objetivos. Por
				vezes isto significa utilizar alguns métodos não ortodoxos.
				<p />
				&emsp; Por outro lado, a raposa é um sinal de estar ciente do nosso
				redor.
				<p />
				&emsp; A raposa é extraordinariamente eficaz e adaptável, a raposa muda
				incrivelmente a sua performance, adaptando-se sempre às novas
				circunstâncias. A raposa totémica diz-nos para não reclamar, mas sim
				adaptar-nos ao nosso ambiente, a raposa funde-se com ele, e é assim com
				o meio ambiente (e as circunstâncias) colocando-os a seu favor.
				<p />
				&emsp; A astúcia, a engenhosidade, a concentração nos objetivos
				importantes da vida e a adaptação ao ambiente é o que o totem da raposa
				traz para aquele que o invoca.
				<p />
				&emsp; Se a criatividade e a engenhosidade forem necessárias, é provável
				que o totem da raposa apareça. A raposa é um totem ideal para ter
				sucesso e desenvolvimento na vida.
			</h1>
		</div>
	);
};
